import React, { useState , useEffect} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Button,
  Container,
  Row,
  Col,
  ListGroup,

} from "react-bootstrap";
import firebase from "./services/firebase";


 const ManagerPage = (props) => {

        const [experimemtDocID, setExpirementerDoc] = useState();
        const [experimemterName, setExpirementerName] = useState("");
        const [experimemtTime, setExpirementTime] = useState("");
        const [experimentUpdatingData, setExpData] = useState()
        const [dataAsLsit, setDataAsList] = useState([])
        const [loaded, setAsLoaded] = useState(false)
        const [dataRenderedasList, setRenderData] = useState([])

    useEffect(() => { 
        console.log ("IN USE EFFECT")
        const db = props.db
        db.orderBy('time', 'desc').limit(1).get().
           then((data) =>{
               const docRef = data.docs[0];
               const docData = docRef.data()
               const docID = docRef.id
               setExpirementerDoc(docID)
               setExpData(data)
               setExpirementerName(docData["fullname"])
               const time = docData.time.split(" ")[1].split(":")
               setExpirementTime(time[0] + ":" + time[1])
               setAsLoaded(true)
               onCollectionUpdate()

               renderData3()

           } 
           )


        //remember to unsubscribe from your realtime listener on unmount or you will create a memory leak
        return () => undefined
    }, []);





     function getLateseDocID(){
        const db = props.db
         db.orderBy('time', 'desc').limit(1).get().
            then((data) =>{
                const docRef = data.docs[0];
                const docData = docRef.data()
                const docID = docRef.id
                setExpirementerDoc(docID)
                setExpirementerName(docData["fullname"])
                const time = docData.time.split(" ")[1].split(":")

                setExpirementTime(time[0] + ":" + time[1])
                setAsLoaded(true)

                onCollectionUpdate()
                // renderData3()


            } 
            )

    }
   

     function onCollectionUpdate() {
         console.log("update: " +experimemtDocID)
         if(experimemtDocID != undefined){
            const doc = props.db.doc(experimemtDocID)
            console.log('doc updated!' + experimemtDocID)

            const observer = doc.onSnapshot(docSnapshot => {
                const data = docSnapshot.data();
                console.log(`Received doc snapshot:${docSnapshot.id} ${data}`);
               // console.log(data)
                setExpData(data)
                renderData3()
                // ...
              }, err => {
                console.log(`Encountered error: ${err}`);
              });

         }
        

      }



      function renderData3(){
        if(experimentUpdatingData){
            console.log("rendering new data")

            const doc = props.db.doc(experimemtDocID)
            console.log('doc updated!' + experimemtDocID)

            const observer = doc.onSnapshot(docSnapshot => {
                const data = docSnapshot.data();
                console.log(`Received doc snapshot:${docSnapshot.id} ${data}`);
               // console.log(data)
                let list = []
                let calibTest = data["calibrationTests"]
                console.log('calib: ')
                console.log( calibTest)
                calibTest.forEach( (item) => {
                    console.log(`${item.testNum} : ${item.value}`)
                    list.push(
                        <li >
                        {item.testNum} : {item.value}
                        </li>
                    )
                })
                setRenderData(list);

                // ...
              }, err => {
                console.log(`Encountered error: ${err}`);
              });
            
            console.log("hiiiiii" ) 

        }
  
      }

      function sortByKey(array, key) {
        let ans =  array.sort(function(a, b) {
            var x = a[key]; var y = b[key];
            return ((x < y) ? -1 : ((x > y) ? 1 : 0));
        });
        return ans
    }
    

    
        return (
            <div>
                <h3>Manager Page</h3>
                <h6>שם הנסיין: {experimemterName} </h6>
                <h6>זמן תחילת הניסוי: {experimemtTime} </h6>

                <Button onClick={getLateseDocID}>
                    הצג פרטי ניסוי נוכחי
                </Button>

                <div>
                    <ul>

                        {
                     

                                dataRenderedasList
                                
                
                        }
                    

                    </ul>

                </div>
            </div>
        );
}

export default ManagerPage;