import firebase from "./services/firebase";
import React, { Component, useState } from "react";
import Slider, { Range } from 'rc-slider';
import 'rc-slider/assets/index.css';

import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Button,
  Container,
  Row,
  Col
} from "react-bootstrap";


const db = firebase.collection("/BGU-lab-dev");

class CalibrationPage extends Component{

  constructor(props) {
    super(props);
    this.state ={
      testCouner: 0,
      sliderValue: 0,

    }
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleReset = this.handleReset.bind(this);

    

  }
  componentDidMount() {
    this.unsubscribe = firebase.collection('/slider-data').onSnapshot(this.onCollectionUpdate) 

  }

  componentWillUnmount() {
    // we have to unsubscribe when component unmounts, because we don't need to check for updates
    this.unsubscribe()
  }

  
  onCollectionUpdate = (querySnapshot) => {
    // we have to update the state
    let value = 0;
    querySnapshot.forEach((document) => {
      const doc= document.data();
      console.log(doc)
      value = doc.value
    })
    this.setState({value:value, sliderValue:value})

  }



  getData() {
    let value;
    db.get().then(function(snapshot) {
      snapshot.forEach( function(childSnapshot){
        db.doc(childSnapshot.id).get().
        then( docSnapshot => {
          [docSnapshot].forEach( doc => {
            const data = doc.data();
            console.log(doc.id, data);
            console.log(`sliderID: ${data.id}, value: ${data.value}`)
            value =  data.value;
          })

        })
        //console.log(`id: ${childSnapshot.id}, data: ${childSnapshot.data}`)
      })
    })
    console.log(value)
    return value
  }



  create(tutorial) {
    return db.add(tutorial);
  }



  update( input) {
    db.get().then(function(snapshot) {
      snapshot.forEach( function(childSnapshot){
        db.doc(childSnapshot.id).update({value:input})
        // thisRef.setState({value:input},
        //    ()=>console.log(`updated value:`+ input) )
        //console.log(`id: ${childSnapshot.id}, data: ${childSnapshot.data}`)
      })
    })
  }

  addTestScoreToDoc2(input){
         db.orderBy('time', 'desc').limit(1).get().
            then((data) =>{
              const docRef = data.docs[0];
              const docData = docRef.data()
              const docID = docRef.id
              let dataToUpdate = docData["calibrationTests"]

              let scoreCounter = `כיול ${this.state.testCouner}`
              this.setState(prevState =>  {return {testCouner: prevState.testCouner+1} })
              let newData ={testNum : ` כיול ${this.state.testCouner}`, value:input }
              //newData[scoreCounter] = input
              dataToUpdate.push(newData)
              const docRefUpdate = db.doc(docRef.id);
              docRefUpdate.update({calibrationTests: dataToUpdate})


            } 
            )
  }
  // addTestScoreToDoc(input){
  //   const docRef = db.doc(this.props.docRefID);
  //   let scoreCounter = `כיול ${this.state.testCouner}`
  //   this.setState(prevState =>  {return {testCouner: prevState.testCouner+1} })
  //   let newData ={}
  //   newData[scoreCounter] = input
  //   let docData = docRef.get().then((doc) =>  {
  //     let data = doc.id
  //     console.log("wowowow " + data.id )}
  //   )
    
  //   // let toUdpate = docRef.calibrationTests
  //   // toUdpate.push(newData)
  //   // docRef.update(
  //   //   {calibrationTests: toUdpate}
  //   // )


  //       //docRef.update(newData)

  //   console.log(`added score: ${input}`)
  // }

 
    
  handleChange(event) {
    this.setState({input: event.target.value}, ()=>{
        this.update(event.target.value,this)
      });
    }

  handleSubmit(event) {
    event.preventDefault();
    //this.update(this.state.sliderValue)
    this.addTestScoreToDoc2(this.state.sliderValue)
    this.setState({sliderValue:0})
    //this.getSliderValue()

  }

  handleReset(event) {
    event.preventDefault();
    //this.setState({sliderValue:0})
    this.setState({sliderValue:0})
    //this.getSliderValue()

  }

  async getSliderValue(){
    const docRef =  firebase.collection("/slider-data").doc('hIGjEMjhpHO604ed6VKp')
    const doc = await docRef.get()
    if(!doc.exists){
      console.log("ERROR! doc doesnt exsits on firestore")
      return 0;
    }
    else{
        const val = doc.data().value;
        this.setState({value:val})
        console.log(val)
        return val;
    }
  }
  delete(id) {
    return db.doc(id).delete();
  }

  onSliderChange = value => {
    this.setState(
      {
        sliderValue:value
      },
      () => {
        console.log(this.state.sliderValue);
      }
    );
  };

  
  render(){
    return (
      <div>
        <Row>
          <Col>
          <form >
          {/* <label>
            Name:
            <input type="text" value={this.state.input} onChange={this.handleChange} />
          </label> */}

          <Button
            variant="outline-primary"
            onClick={this.handleSubmit}
          >
            Submit
          </Button>

          <Button
            variant="outline-primary"
            onClick={this.handleReset}>
              Reset
          </Button>

        </form>
   


          </Col>
        </Row>
        <Row>
          <Col style = {{width:"200px"}}>

              <div >
              <Slider
              min={0}
              max={10}
              defaultValue={0}
              value={this.state.sliderValue}
              onChange={this.onSliderChange}
              step ={0.1}
              reverse = {true}
              railStyle={{ backgroundColor: 'grey', height: 200 }}
              trackStyle={{ backgroundColor: 'red', height: 200 }}
              handleStyle={{
                borderColor: 'black',
                height: 200,
                width: 20,
                marginLeft: -14,
                marginTop: 0,
                backgroundColor: 'white',
              }}


              />
            </div>

            {/* <div>
            text value: {this.state.value} 

            </div> */}

            <div>

              {/* slider value: {this.state.sliderValue} */}
            </div>
          </Col>
        </Row>
      </div>
    )
  }
}

export default CalibrationPage;